<template>
    <section class="toy-edit">
       <form v-if="toyToEdit" @submit.prevent="setToy">
            <input  ref="input" placeholder="Toy Name?" v-model="toyToEdit.name" type="text">
            <input  placeholder="Price" v-model.number="toyToEdit.price" type="number">
            <input  placeholder="Type" v-model="toyToEdit.type" type="text">
            <button>Save</button>
        </form>
        <router-link class="back-btn" to="/toy">Back</router-link>
    </section>
</template>

<script>

import { toyService } from '../services/toy.service.js'
 
export default {
       data() {
        return {
            toyToEdit: null
        }
    },
    created() {
        this.getToy()
    },
    methods: {
        getToy() {
            const toyId = this.$route.params.toyId;
            if (toyId) {
                toyService.getById(toyId)
                    .then(toy => {
                        this.toyToEdit = toy
                    })
                    .then(() => this.$refs.input.focus())
            }
            else {
                toyService.getEmptytoy()
                    .then(toy => {
                        this.toyToEdit = toy
                    })
                    .then(() => this.$refs.input.focus())
            }
        },
        setToy() {
            if (!this.toyToEdit._id) this.toyToEdit.createdAt = Date.now()
            this.$store.dispatch({ type: 'setToy', savedToy: JSON.parse(JSON.stringify(this.toyToEdit)) })
                .then(savedToy => {
                    // console.log('savedtoy:', savedToy)
                    // showMsg('Saved toy successfully')
                    this.$router.push('/toy')
                })
                .catch(err => {
                    console.log('err:', err)
                    // showMsg('Cannot save toy', 'danger')
                })
        }
    },
    computed: {
    },
    mounted() {
    }
}
</script>