
<template>
     <section class="toy-filter flex justify-center align-center">
        <input @input="setFilter" ref="filterInput" v-model="filterBy.searchStr" placeholder="Search a toy" type="text">
        <label>Status: </label>
        <select @change="setFilter" v-model="filterBy.status" value="All">
            <option value="All">All</option>
            <option value="In stock">In stock</option>
            <option value="Out of stock">Out of stock</option>
        </select>
        <label>Type: </label>
        <select @change="setFilter" v-model="filterBy.type" value="All">
            <option value="All">All</option>
            <option value="Funny">Funny</option>
            <option value="Adult">Adult</option>
            <option value="Educational">Educational</option>
        </select>
        <label>Sort by: </label>
        <select @change="setFilter" v-model="filterBy.sortBy" value="None">
            <option value="Name">Name</option>
            <option value="Price">Price</option>
        </select>
        </section>
</template>



<script>

// import { toyService } from '../services/toy.service.js'

export default {
      data() {
        return {
            filterBy: {
                searchStr: '',
                status: 'All',
                type: 'All',
                sortBy: 'Name'
            }
        }
    },
    created() {
        // this.setFilter = toyService.debounce(this.setFilter, 1000)
    },
    methods: {
        setFilter() {
            this.$store.commit({type: 'setFilter', filterBy: JSON.parse(JSON.stringify(this.filterBy))})
            this.$store.dispatch( {type: 'loadToys'}) // add catch
        }
    },
    computed: {
    },
    mounted() {
        this.$refs.filterInput.focus();
    }
}
</script>

