<template>
     <section v-if="toy" class="toy-details">
            <h2>Name: {{ toy.name}}</h2>
            <h3>Type: {{ toy.type }}</h3>
            <h3>Price: {{ toy.price }}</h3>
            <h3>In stock: {{ toy.inStock }}</h3>
            <h3>Created At: {{ toy.createdAt }}</h3>
            <router-link class="back-btn" to="/toy">Back</router-link>
        </section>
</template>


<script>
export default {
      data() {
        return {
            toy: null
        }
    },
    methods: {
        getToy(toyId) {
            this.$store.dispatch({type: 'getToy', toyId})
                .then(toy => this.toy = toy)
        }
    },
    computed: {
        // createdAt() {
        //     return moment(this.toy.createdAt)
        // }
    },
    created() {
        const toyId = this.$route.params.toyId
        // console.log('toyId:', toyId)
        this.getToy(toyId)
    }
}
</script>