<template>
  <section class="toy-app flex column">
    <toy-filter />
    <toy-list v-if="toysToShow" @remove="removeToy" :toys="toysToShow" />
    <div class="paging-btns self-center flex space-between">
      <button @click="setPage(-1)">Previous</button>
      <button @click="setPage(1)">Next</button>
    </div>
  </section>
</template>

<script>
import toyList from "@/cmps/toy-list.vue";
import toyFilter from "@/cmps/toy-filter.vue";

export default {
  computed: {
    toysToShow() {
      return this.$store.getters.toysForDisplay;
    },
  },
  methods: {
    removeToy(toyId) {
      this.$store.dispatch({ type: "removeToy", toyId });
    },
    // setNextPage() {
    //   this.$store.commit({ type: "setNextPage" });
    //   this.$store.dispatch({ type: "loadToys" });
    // },
    // setPrevPage() {
    //   this.$store.commit({ type: "setPrevPage" });
    //   this.$store.dispatch({ type: "loadToys" });
    // },
    setPage(diff) {
      const type = diff > 0 ? "setNextPage" : "setPrevPage";
      this.$store.commit({ type });
      // this.$store.dispatch({ type: "loadToys" });
    },
  },
  components: {
    toyList,
    toyFilter,
  },
};
</script>