<template>
     <section class="toy-preview flex align-center column">
            <h3>Name: {{ toy.name }} </h3>
            <h3>Price: {{ toy.price }} </h3>
            <h3>Type: {{ toy.type }} </h3>
            <h3>Status: {{ toy.inStock }} </h3>
            <div class="preview-btns flex column align-center">
                <router-link class="edit-btn" :to="'/toy/edit/'+toy._id">Edit</router-link>
                <router-link class="details-btn" :to="'/toy/'+toy._id">Details</router-link>
                <button @click="remove">Delete</button>
            </div>
        </section>   
</template>



<script>
export default {
    props: ['toy'],
      methods: {
        remove() {
            this.$emit('remove', this.toy._id)
        },
        // mark() {
        //     this.$emit('mark', JSON.parse(JSON.stringify(this.toy)))
        // },
    },
}
</script>